import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

export default function MoviesPage({ API_KYI }) {
  const [id, setId] = useState(null);
console.log(id);
  const { movieId } = useParams();
  console.log(movieId);

  useEffect(
    () =>
      fetch(
        `https://api.themoviedb.org/3/movie/${movieId}?api_key=${API_KYI}&language=en-US`,
      )
        .then(response => response.json())
        .then(
          res =>
            setId(res.results),
          // console.log(res)
        ),
    [],
  );
console.log(id);

  return <div>MoviesPage{id}</div>;
}
